@media only screen and (max-width: 1140px) {
    .App-header {
        max-width: 100%;
        height: 70px;
        padding: 4px 0 1px;
    }

    .logoContainer {
        overflow: hidden;
        width: 22%;
    }
    .logoContainer img {
        margin-top: px;
        margin-left: -10px;
        height: 140%;
    }

    .menuContainer button{
        margin-right: 14px;
    }

    ul {
        /* display: none; */
        background: #8ECCEC;
        position: fixed;
        top: -22px;
        left: 103%;
        width: 80vw;
        height: 100vh;
        padding: 40px;
    }

    .menuContainer li {
        display: block;
        color: black;
    }

    .menuContainer .dropdownParent, .hoverMenu button {
        color: black;
        font-size: 18px;
        margin-bottom: 10px;
    }

    ul span {
        display: none;
        /* display: inline-block; */
    }

    .dropdownParent span {
        display: inline-block;
        color: black;
        font-size: 20px;
    }

    .App-container, .App-header {
        transition: transform 200ms cubic-bezier(0.145, 0.555, 0.375, 1.125);
        /* transform: translateX(-80%);
        transform: translateX(-170%); */
    }

    .hoverMenu {
        display: block;
        position: fixed;
        background: orange;
        top: -22px;
        left: 676%;
        width: 90vw;
        height: 100vh;
        border: none;
        padding: 40px;
    }
}