/* MARK: Hero */

.heroRow {
  height: 600px; 
}

.heroRow .videoContainer {
  height: 100%;
  background-color: green;
  display: inline-block;
  /* float: left; */
}

.videoContainer {
  overflow: hidden;
  width: calc(50% + (100vw - 1140px)/2);
  margin-left: calc((-100vw + 1140px)/2);
  /* transform: translateX(-50%) */
  /* width: 100%; */
}

.videoContainer .video {

}

.video img {
  -webkit-clip-path: url(#heroClip);
          clip-path: url(#heroClip);
  min-width: 100%;
  min-height: 100%;
  -webkit-transform: scaleX(-1);
          transform: scaleX(-1);
  /* width: 100%; */
}

.heroRow .heroTextContainer {
  display: inline-block;
  height: 100%;
  float: right;
}

.heroText {
  display: block;
  position: relative;
  margin: 0px 10% 50px 50px;
  top: 40%;
  -webkit-transform: translateY(-48%);
          transform: translateY(-48%);
}

.heroText h1 {
  font-size: 72px;
  font-weight: bold;
}

.heroText p {
  /* margin-top: 60px; */
  font-size: 36px;
  line-height: 1.22;
}

.heroText button {
  font-size: 24px;
  font-weight: bold;
  color: white;
  text-transform: uppercase;
  padding: 6px 30px;
  border: none;
  background-color: #67DDAB;
}

/* MARK: Summary Row */
.summaryRow {
  width: 100%;
  height: auto;
  transition: width 1s ease;
}

.postIts {
  vertical-align: top;
  width: 60%;
  padding: 80px 0 0px 80px;
  /* transform: translateY(-30%); */
}

.summaryRow img {
  width: 100%;
}

.summaryBackground {
  position: absolute;
  z-index: -3;
  left: 0px;
  margin-top: -60px;
  margin-bottom: 100px;
  height: 120%;
  width: 100vw;

  background-repeat: no-repeat;
  background-image: linear-gradient(#8ECCEC, #8ECCEC);
  background-size: 0% 100%;
  transition: background-size 0.8s;
}

.active .summaryBackground {
  background-size: 100% 100%;
}

.summaryBackground svg {
  width: 1440px;
  z-index: -1;
  float: left;
  margin-top: -120px;
  margin-right: -80px;
  margin-left: calc(70px + 30%);
  position: absolute;
  -webkit-clip-path: url(#clipper);
          clip-path: url(#clipper);
}

.summaryBackground path {
  fill: none;
  stroke: red;
  stroke-width: 2px;
  stroke-dasharray: 10px;
  /* animation: pathSlow 5s linear infinite; */
  transition: stroke-linecap 1s, stroke-width 1s;
}

.summaryBackground path {
  stroke: white;
  stroke-dasharray: 1740px;
  stroke-dashoffset: 0px;
  transition: stroke-dashoffset 3s 0.4s, stroke-dasharray 2s 0.4s;
  /* animation: pathSlow 3.2s linear infinite;  */
}

.inspiration, .workshop {
  z-index: -10;
}

.popUp {
  z-index: -1;
}

.inspiration svg, .workshop svg, .popUp svg {
  z-index: -5;
  margin-left: 0;
  width: 104%;
  margin-top: 0%;
}

.workshop svg {
  margin-top: -10%;
}

.inspiration path {
  stroke: #FF9C1C;
}

.workshop path {
  stroke: #67DDAB;
}
.popUp svg {
  margin-top: -240px;
}

.popUp path {
  stroke: #8ECCEC;
}

.inspiration .backPath path, .workshop .backPath path, .popUp .backPath path {
  stroke: white;
}


.active path {
  stroke-dashoffset: 0px;
  /* stroke-dasharray: 10px; */
}

.backPath path {
  opacity: 1;
  stroke: #8ECCEC;
  stroke-width: 4px;
  stroke-dasharray: 10px;
  stroke-dashoffset: 0px;
  transition: opacity 0.2s 0.4s;
  -webkit-animation: pathSlow 3.2s linear infinite;
          animation: pathSlow 3.2s linear infinite; 
}

.active .backPath path {
  opacity: 1;
}

@-webkit-keyframes pathSlow {
  100% {
    stroke-dashoffset: -20px;
  }
}

@keyframes pathSlow {
  100% {
    stroke-dashoffset: -20px;
  }
}

.summary {
  display: inline-block;
  width: 40%; 
  margin-top: 15%;
  opacity: 0;
  /* margin-top: 50%; */
  -webkit-transform: translateY(20px);
          transform: translateY(20px);
  transition: opacity 1s, -webkit-transform 1s;
  transition: transform 1s, opacity 1s;
  transition: transform 1s, opacity 1s, -webkit-transform 1s;
}

.summary h2 {
  font-size: 24px;
  font-weight: bold;
  color: white;
}

.summary h1 {
  font-size: 48px;
  font-weight: bold;
}

.summary p {
  margin-top: 30px;
  font-size: 24px;
}

.active .summary {
  -webkit-transform: translateY(0);
          transform: translateY(0);
  opacity: 1;
}

/* MARK: Workshop Cards */
.lightbulbAnimation {
  width: 600px;
  display: block;
  margin: auto;
  -webkit-transform: translateX(0px);
          transform: translateX(0px);
  overflow: hidden;
  padding-top: 240px;
  margin-top: -200px;
  margin-bottom: -100px;
}

@-webkit-keyframes spriteSheet-6frame {
  0% {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
  100% {
    -webkit-transform: translateX(calc(-605.1px*6));
            transform: translateX(calc(-605.1px*6));
  }
}

@keyframes spriteSheet-6frame {
  0% {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
  100% {
    -webkit-transform: translateX(calc(-605.1px*6));
            transform: translateX(calc(-605.1px*6));
  }
}

.lightbulbAnimation img:hover {
  -webkit-animation: spriteSheet-6frame 0.5s steps(6);
          animation: spriteSheet-6frame 0.5s steps(6);
}

.lightbulbAnimation img {
  height: 100%;
  /* margin-left: -100px; */
  /* transform: translateX(-605px); */
}

