    border: none;
    outline: none;
    display: block;
}

.Contact.pending .form .submitButton {
    color: white;
    -webkit-animation: submitting 1s infinite;
            animation: submitting 1s infinite;
}

@-webkit-keyframes submitting {
    0% {
        background: #070707;
    }
    25% {
        background: #67DDAB;
    }
    50% {
        background: #8eccec;
    }
    75% {
        background: #e1942f;
    }
    100% {
        background: #070707;
    }
}

@keyframes submitting {
    0% {
        background: #070707;
    }
    25% {
        background: #67DDAB;
    }
    50% {
        background: #8eccec;
    }
    75% {
        background: #e1942f;
    }
    100% {
        background: #070707;
    }
}

.form .submitButton:hover {
    color: white;
    background: #070707;
}

